

/* 전체 컨테이너 스타일 */
.translation-container {
  max-width: 72rem; /* max-w-6xl와 동일 */
  margin: 0 auto;
  padding: 1.5rem;
}

.logo {
  width: 10rem;
  margin: 0 auto;
}

/* 헤더 스타일 */
.app-title {
  font-size: 2.25rem;
  font-weight: 700;
  color: #3b82f6; /* Tailwind's blue-500 */
  text-align: center;
  margin-top: 2rem;
}

.app-subtitle {
  color: #4b5563; /* Tailwind's gray-600 */
  margin-top: 0.5rem;
  text-align: center;
}

/* 입력 및 출력 textarea 스타일 */
.translation-textarea {
  width: 100%;
  height: 16rem;
  padding: 1rem;
  border: 1px solid #d1d5db; /* Tailwind's gray-300 */
  border-radius: 0.375rem;
  font-size: 2.25rem;
  resize: none;
}

/* 버튼 스타일 */
.translate-button, 
.refresh-button {
  width: 5rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  transition: background-color 0.3s ease;
}

/* TranslationComponent.css */

.translation-container {
  max-width: 60rem;
  margin: 0 auto;
  padding: 1.5rem;
}

.translation-wrapper {
  margin-top: 2rem;
}

.translation-grid {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 1rem;
  align-items: start;
}

.translation-textarea {
  width: 100%;
  height: 16rem;
  padding: 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  font-size: 2.3rem;
  resize: none;
}

.button-center-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  height: 100%;
}

.translate-button, 
.refresh-button {
  width: 5rem;
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  font-size: 0.8rem;
  transition: background-color 0.3s ease;
}

.translate-button {
  background-color: #3b82f6;
  color: white;
}

.translate-button:hover {
  background-color: #2563eb;
}

.translate-button:disabled {
  background-color: #d1d5db;
  cursor: not-allowed;
}

.refresh-button {
  background-color: #3b82f6;
  color: white;
}

.refresh-button:hover {
  background-color: #2563eb;
}

.refresh-button:disabled {
  background-color: #e5e7eb;
  cursor: not-allowed;
}

.refresh-count {
  margin: 0.5rem 0;
  color: #4b5563;
}

/* 기존의 다른 스타일들 유지 */
.app-title {
  font-size: 2.25rem;
  font-weight: 700;
  color: #3b82f6;
}

.app-subtitle {
  color: #4b5563;
  margin-top: 0.5rem;
}

/* 번역 기록 스타일 */
.history-container {
  margin-top: 2rem;
}

.history-title {
  font-size: 1.125rem;
  font-weight: 500;
  margin-bottom: 1rem;
}

.history-item {
  padding: 1rem;
  border-radius: 0.375rem;
  background-color: #f3f4f6; /* gray-50 */
  font-size: 1.125rem;
  margin-bottom: 1rem;
}

.translation-grid.input-only {
  grid-template-columns: 1fr auto;
}

.translation-grid.with-output {
  grid-template-columns: 1fr auto 1fr;
}

.translation-textarea.input {
  height: 16rem; /* 더 큰 높이로 설정 */
}

.translation-grid.with-output .translation-textarea.input {
  height: 16rem; /* 출력 창이 있을 때 높이를 줄임 */
}

