/* 기존 헤더 스타일 */
.header {
  text-align: center;
}

.subtitle-container {
  display: inline-flex; /* 제목과 ? 버튼을 가로로 배치 */
  align-items: center;
  gap: 0.5rem; /* 간격 설정 */
}

.tooltip-container {
  position: relative; /* 툴팁 내용 위치 기준 */
  display: inline-block;
}

.tooltip-button {
  background-color: #3b82f6; /* 파란색 버튼 */
  color: white;
  border: none;
  border-radius: 50%; /* 동그란 버튼 */
  width: 1.3rem;
  height: 1.3rem;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-weight: bold;
}

.tooltip-button:hover {
  background-color: #2563eb; /* 호버 시 더 진한 파란색 */
}

.tooltip-content {
  visibility: hidden; /* 기본적으로 숨김 */
  opacity: 0; /* 투명 처리 */
  position: absolute;
  background-color: #f9fafb; /* 흰색 배경 */
  color: #1f2937; /* 검은 텍스트 */
  text-align: left;
  padding: 0.5rem;
  border: 1px solid #d1d5db; /* 회색 테두리 */
  border-radius: 0.375rem;
  width: 27rem; /* 고정된 너비 */
  z-index: 1000;
  top: 2rem; /* 버튼 아래로 배치 */
  left: 50%;
  transform: translateX(-50%);
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.tooltip-container:hover .tooltip-content {
  visibility: visible; /* 호버 시 보임 */
  opacity: 1; /* 불투명 처리 */
}

.tooltip-content {
  /* 기존 스타일 유지 */
  visibility: hidden;
  opacity: 0;
  position: absolute;
  background-color: #f9fafb;
  color: #1f2937;
  text-align: left;
  padding: 1rem;
  border: 1px solid #d1d5db;
  border-radius: 0.375rem;
  width: 25rem;
  z-index: 1000;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.tooltip-content a img {
  width: 1.5rem; /* 아이콘 크기 조정 */
  height: 1.5rem;
  margin-left: 0.5rem; /* 링크 간격 설정 */
}

.tooltip-content .link-container {
  display: flex;
  align-items: center;
  margin-top: 0.5rem; /* 위와 간격 */
}
